export const getAgeByBrithday = (strBirthday) => {
  var returnAge
  var strBirthdayArr = strBirthday.split('-')
  var birthYear = strBirthdayArr[0]
  var birthMonth = strBirthdayArr[1]
  var birthDay = strBirthdayArr[2]
  // var birthD
  var d = new Date()
  var nowYear = d.getFullYear()
  var nowMonth = d.getMonth() + 1
  var nowDay = d.getDate()

  if (nowYear == birthYear) {
    let birthdayBase = this.formatDate(new Date())
    returnAge = (this.DateDiff(birthdayBase, strBirthday) + 1) + ''
    return returnAge// 
  } else {
    var ageDiff = nowYear - birthYear // 
    if (ageDiff > 0) {
      if (nowMonth == birthMonth) {
        var dayDiff = nowDay - birthDay// 
        if (dayDiff < 0) {
          returnAge = (ageDiff - 1) + ''
        } else {
          returnAge = ageDiff + ''
        }
      } else {
        var monthDiff = nowMonth - birthMonth// 
        if (monthDiff < 0) {
          returnAge = (ageDiff - 1) + ''
        } else {
          returnAge = ageDiff + ''
        }
      }
    } else {
      returnAge = -1// -1  
    }
  }
  return returnAge// 
}
