
import moment from 'moment'
export const dateFormat = {
  methods: {
    dateFormat (val) {
      return moment(val).format('YYYY-MM-DD')
    }
  }
}
export const getSexByName = {
  methods: {
    getSexByName (type) {
      if (type === 1) {
        return ''
      } else {
        return ''
      }
    }
  }
}

export const getRelation = {
  methods: {
    getRelation (type) {
      switch (parseInt(type)) {
        case 0:
          return ''
          break
        case 1:
          return ''
          break
        case 2:
          return ''
          break
        case 3:
          return ''
          break
        case 4:
          return ''
          break
        case 5:
          return ''
          break
        case 6:
          return ''
          break
        default:
          return ''
      }
    }
  }
}

