import { getAgeByBrithday } from './date'
/**
 * 
 */
class ValidateClazz {
  /**
   * 
   * ,
   * @param code 
   * @returns {boolean} true:  false:
   */
  idCardRegexs (code) {
    let city = {
      11: '',
      12: '',
      13: '',
      14: '',
      15: '',
      21: '',
      22: '',
      23: ' ',
      31: '',
      32: '',
      33: '',
      34: '',
      35: '',
      36: '',
      37: '',
      41: '',
      42: ' ',
      43: '',
      44: '',
      45: '',
      46: '',
      50: '',
      51: '',
      52: '',
      53: '',
      54: ' ',
      61: '',
      62: '',
      63: '',
      64: '',
      65: '',
      71: '',
      81: '',
      82: '',
      91: ' '
    }
    const errorTips = ''
    /*  */
    let powers = ['7', '9', '10', '5', '8', '4', '2', '1', '6', '3', '7', '9', '10', '5', '8', '4', '2']
    /* 18 */
    let parityBit = ['1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2']
    let tip = ''
    let pass = true
    function checkAddressCode (addressCode) {
      var check = /^[1-9]\d{5}$/.test(addressCode)
      if (!check) return false
      if (city[parseInt(addressCode.substring(0, 2))]) {
        return true
      } else {
        return false
      }
    }
    function checkBirthDayCode (birDayCode) {
      var check = /^[1-9]\d{3}((0[1-9])|(1[0-2]))((0[1-9])|([1-2][0-9])|(3[0-1]))$/.test(birDayCode)
      if (!check) return false
      var yyyy = parseInt(birDayCode.substring(0, 4), 10)
      var mm = parseInt(birDayCode.substring(4, 6), 10)
      var dd = parseInt(birDayCode.substring(6), 10)
      var xdata = new Date(yyyy, mm - 1, dd)
      if (xdata > new Date()) {
        return false// 
      } else if ((xdata.getFullYear() == yyyy) && (xdata.getMonth() == mm - 1) && (xdata.getDate() == dd)) {
        return true
      } else {
        return false
      }
    }
    /*  */
    function getParityBit (idCardNo) {
      var id17 = idCardNo.substring(0, 17)
      /*  */
      var power = 0
      for (var i = 0; i < 17; i++) {
        power += parseInt(id17.charAt(i), 10) * parseInt(powers[i])
      }
      /*  */
      var mod = power % 11
      return parityBit[mod]
    }
    /* 1518 */
    function getId18 (idCardNo) {
      if (idCardNo.length == 15) {
        var id17 = idCardNo.substring(0, 6) + '19' + idCardNo.substring(6)
        var parityBit = getParityBit(id17)
        return id17 + parityBit
      } else if (idCardNo.length == 18) {
        return idCardNo
      } else {
        return null
      }
    }
    if (!code || !/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(code)) {
      tip = errorTips
      pass = false
    } else if (!city[code.substr(0, 2)]) {
      tip = errorTips
      pass = false
    } else {
      // 18
      if (code.length === 15) {
        var check = /^[1-9]\d{7}((0[1-9])|(1[0-2]))((0[1-9])|([1-2][0-9])|(3[0-1]))\d{3}$/.test(code)
        if (!check) { pass = false }
        var addressCode = code.substring(0, 6)
        check = checkAddressCode(addressCode)
        if (!check) {
          return { pass: false, tip: '' }
        }
        var birDayCode = '19' + code.substring(6, 12)
        check = checkBirthDayCode(birDayCode)
        if (!check) {
          return { pass: false, tip: '' }
        }
        code = getId18(code)
      }
      if (code.length === 18) {
        code = code.split('')
        // (aiWi)(mod 11)
        // 
        let factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2]
        // 
        let parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2]
        let sum = 0
        let ai = 0
        let wi = 0
        for (let i = 0; i < 17; i++) {
          ai = code[i]
          wi = factor[i]
          sum += ai * wi
        }
        let last = parity[sum % 11]
        if (last != code[17]) {
          tip = errorTips
          pass = false
        }
      }
    }
    // return pass
    return { pass: pass, tip: tip }
  }
  mobileRegexs (mobile) {
    const errorTip = ''
    const regMobile = /^1[3|4|5|6|7|8|9]\d{9}$/
    if (mobile && !regMobile.test(mobile)) {
      return { pass: false, tip: errorTip }
    } else {
      return { pass: true, tip: '' }
    }
  }
  pwdRegexs (pwd) {
    const errorTip = ''
    const regPwd = /^[\da-zA-Z0-9~`!@#/'|\[\]:,;$%^&*<>()}{?=\-+]{4,20}$/
    if (!regPwd.test(pwd) && pwd) {
      return { pass: false, tip: errorTip }
    } else {
      return { pass: true, tip: '' }
    }
  }

  years16Regexs (idNum) {
    let age
    let pass = false
    const yearsErrorTips = '16'
    if (idNum.length == 15) {
      idNum = idNum.substring(0, 6) + '19' + idNum.substring(6)
    }
    let birthday =
      idNum.slice(6, 10) +
      '-' +
      idNum.slice(10, 12) +
      '-' +
      idNum.slice(12, 14)
    age = getAgeByBrithday(birthday)
    if (age == '-1') {
      return { pass: pass, tip: yearsErrorTips }
    } else if (age.includes('')) {
      return { pass: pass, tip: yearsErrorTips }
    } else if (age.includes('')) {
      if (parseInt(age) < 16) {
        return { pass: pass, tip: yearsErrorTips }
      } else {
        return { pass: true, tip: '' }
      }
    }
  }
  /**
   * 
   * @param str 
   * @param type 
   * @returns {boolean}
   */
}

export const ValidateUtil = new ValidateClazz()

/**
 * 
 */
export default {
  ValidateUtil

}
